.modal {
    position: fixed;
    display: block;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 2;
    /* overflow-y: auto; */
    /* background-color: rgba(128, 128, 128, 0.5); */
}

.modal_background {
    min-height: 100vh;
    width: 100%;
    background-color: rgba(128, 128, 128, 0.5);
    clear: both;
}

.modal_body {
    position: absolute;
    left: 0;
    top: 0;
    height: 80%;
    margin: 10% 0 10% 20%;
    overflow-y: scroll;

    width: 60%;
    background: #0f2027; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(
        linear,
        left top, right top,
        from(#2c5364),
        color-stop(#203a43),
        to(#0f2027)
    );
    background: linear-gradient(
        to right,
        #2c5364,
        #203a43,
        #0f2027
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    box-shadow: 10px 10px 25px 0px rgba(0, 0, 0, 0.75);
}

.modal_content {
    height: 100%;
    width: 100%;
    overflow-y: visible !important;
    overflow-y: initial !important;
}

.modal_desktop_image {
    width: 100%;
    height: 30vw;
    padding: 8px 5px 10px 5px;
    background-color: black;
    border-radius: 5px;
}

.modal_detail_image {
    width: 60%;
    height: 35%;
    padding: 8px 5px 10px 5px;
    background-color: black;
    border-radius: 5px;
}

.modal_project_image {
    height: 100%;
    width: 100%;
    background-position: top, top;
    background-size: 100% auto;
    background-repeat: no-repeat;
}

.modal_name {
    font-family: 'Courier New', Courier, monospace;
    color: white;
    font-size: 20px;
    height: 2rem;
    max-width: 80%;
    background-color: rgba(0, 0, 0, 0.7);
    margin: -2.5rem 0 3rem 5px;
    padding: 2px 0 0 20px;
    -webkit-clip-path: polygon(0 0, 98% 0, 100% 100%, 2% 100%);
    clip-path: polygon(0 0, 98% 0, 100% 100%, 2% 100%);
}

.modal_description {
    color: white;
    width: 100%;
    padding: 2% 5% 5% 5%;
}

@media only screen and (max-width: 600px) {
    .modal_body {
        position: absolute;
        left: 0;
        top: 0;
        height: auto;
        min-height: 80%;
        width: 90%;
        margin: 10% 0 0 5%;
        background: #0f2027; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
        background: -webkit-gradient(
            linear,
            left top, right top,
            from(#2c5364),
            color-stop(#203a43),
            to(#0f2027)
        );
        background: linear-gradient(
            to right,
            #2c5364,
            #203a43,
            #0f2027
        ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    }

    .modal_desktop_image {
        width: 100%;
        height: 50vw;
        padding: 8px 5px 10px 5px;
        background-color: black;
        border-radius: 5px;
        margin: 0;
    }

    .modal_detail_image {
        width: 100%;
        height: 50vw;
        padding: 8px 5px 10px 5px;
        background-color: black;
        border-radius: 5px;
        margin: 0;
    }
}

@media only screen and (min-width: 601px) {
    ::-webkit-scrollbar {
        width: 10px;
    }

    ::-webkit-scrollbar-track {
        background: #f1f1f1;
    }

    ::-webkit-scrollbar-thumb {
        background: #888;
    }

    ::-webkit-scrollbar-thumb:hover {
        background: #555;
    }
}

/* Fade in animation */
@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@-webkit-keyframes fadein {
    /* Safari and Chrome */
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

/* Fade out animation */
@keyframes fadeout {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@-webkit-keyframes fadeout {
    /* Safari and Chrome */
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

.sub_title {
    color: white;
    display: inline-block;
    font-family: 'Courier New', Courier, monospace;
    font-size: 18px;
    margin: 0 0 0 5%;
    border-bottom: 1px dotted white;
}

.switcher {
    margin: 0 0 0 35%;
    width: 30%;
}

.all_button,
.featured_button {
    display: inline-block;
    width: 50%;
    font-size: 16px;
    height: 28px;
    overflow: hidden;
    color: white;
    font-family: 'Courier New', Courier, monospace;
    transition: font-size 0.25s;
    -webkit-transition: font-size 0.25s;

    background-color: black;
    text-align: center;
    padding: 2px;

    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently */
}

.all_button:hover,
.featured_button:hover {
    font-size: 18px;
    background-color: rgb(27, 27, 27);
    cursor: pointer;
}

.featured_button {
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border-right: 1px dotted white;
}

.all_button {
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}

.project_container {
    animation: fadein 1.5s;
    -moz-animation: fadein 1.5s; /* Firefox */
    -webkit-animation: fadein 1.5s; /* Safari and Chrome */
    -o-animation: fadein 1.5s; /* Opera */
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@-webkit-keyframes fadein {
    /* Safari and Chrome */
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

